import React from 'react'
import { Link } from 'react-router-dom'
import './Clients.css'

function OurClientslist(props) {
    return (
        <>
            <div className="swiper-slide brand-logo brand-logo--slider">
                <Link to={props.path}>
                    <div className="brand-logo__image">
                        <img src={props.img} className="img-fluid" alt="" />
                    </div>
                    <div className="brand-logo__image-hover">
                        <img src={props.imghover} className="img-fluid" alt="" />
                    </div>
                </Link>
            </div>
        </>
    )
}


export default OurClientslist
