import React from 'react'
import '../../App.css'
import { TriangleBottom, TriangleTop } from '../Triangle'


function Banner(props) {
    return (
        <>

            <div class="row-separators-wrap triangle-top  big-triangle-style row-separators-bg11 separators-space" 
            style={{ paddingTop: 90, paddingBottom: 140, backgroundImage: `url(${props.image})` }}>


                {/* <TriangleTop /> */}


                <div class="about-banner-wrap banner-space about-us-top-bg"
                // style={{ backgroundImage: `url(${props.image})` }}
                >
                    <div class="container">
                        <div class="row">
                            <div class="col-lg-8 ml-auto mr-auto">
                                <div class="about-banner-content text-center">
                                    <h1 class="mb-15 text-white page-title">{props.title}</h1>
                                    <h5 class="font-weight--normal text-white">{props.detail} </h5>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <TriangleBottom />
            </div>
        </>
    )
}

export default Banner
