import React from 'react'

import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';

import OurClientslist from './OurClientslist';
import client1 from '../../assets/images/brand/mitech-client-logo-05.png'
import client1_h from '../../assets/images/brand/mitech-client-logo-05-hover.png'
import AppTheme from '../../config/Theme';
import './Clients.css'
import Heading from '../Heading';


const responsive = {
    desktop: {
        breakpoint: { max: 3000, min: 1024 },
        items: 6,
        slidesToSlide: 1 // optional, default to 1.
    },
    tablet: {
        breakpoint: { max: 1024, min: 464 },
        items: 3,
        slidesToSlide: 1 // optional, default to 1.
    },
    mobile: {
        breakpoint: { max: 464, min: 0 },
        items: 1,
        slidesToSlide: 1 // optional, default to 1.
    }
};


function OurClients(props) {
    const { className} = props
    return (
        <>
            <div className={`brand-logo-slider-area pt-40 pb-50 ${className}`}  style={{ background: AppTheme.bgwhite }}>
                <div className="container">

                    <Heading titleBlack="Our" titleBlue="Clients" />


                    <div className="row">
                        <div className="col-lg-12">

                            <div className="brand-logo-slider__container-area">
                                <div className="swiper-container brand-logo-slider__container">
                                    <div className="swiper-wrapper brand-logo-slider__one">


                                        <Carousel
                                            // deviceType={deviceType}
                                            ssr={true}
                                            slidesToSlide={1}
                                            containerClass="carousel-with-custom-dots"
                                            responsive={responsive}
                                            partialVisible={true}
                                            infinite={true}
                                            autoPlay={true}
                                            showDots={false}
                                            removeArrowOnDeviceType={["tablet", "desktop"]}
                                            keyBoardControl={true}
                                            autoPlaySpeed={2000}
                                            transitionDuration={1000}
                                        // centerMode={true}
                                        >


                                            <OurClientslist
                                                path='/'
                                                img={client1}
                                                imghover={client1_h}
                                            />
                                            <OurClientslist
                                                path='/'
                                                img={client1}
                                                imghover={client1_h}
                                            />
                                            <OurClientslist
                                                path='/'
                                                img={client1}
                                                imghover={client1_h}
                                            />
                                            <OurClientslist
                                                path='/'
                                                img={client1}
                                                imghover={client1_h}
                                            />
                                            <OurClientslist
                                                path='/'
                                                img={client1}
                                                imghover={client1_h}
                                            />
                                            <OurClientslist
                                                path='/'
                                                img={client1}
                                                imghover={client1_h}
                                            />
                                            <OurClientslist
                                                path='/'
                                                img={client1}
                                                imghover={client1_h}
                                            />

                                        </Carousel>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default OurClients
